<template>
  <component
    :is="comp"
    :type="type"
    v-bind="$attrs"
    v-on="$listeners"
    @click="onAction"
    :loading="actionLoading || loading"
    :disabled="disabled"
  >
    <slot/>
  </component>
</template>

<script>
export default {
  name: 'LoadingAction',
  data: () => ({
    actionLoading: false,
    disabled: false,
  }),
  props: {
    /**
     *  loading 
     */
    action: {
      type: Function,
      required: true,
    },
    /**
     * 
     */
    comp: {
      type: String,
      default: 'el-button',
    },
    /**
     *  type props  Button type 
     *  type  Button  type=primary
     *  html button  type  primary
     */
    type: String,
    loading: Boolean,
    /**
     * action 
     *  true
     *  <= 0
     */
    disableOnSuccess: {
      type: [String, Number],
      default: 500,
    },
  },
  beforeDestroy() {
    this.clearRecoverDisabledTimeout()
  },
  methods: {
    async onAction() {
      if (this.actionLoading || this.disabled) {
        return false
      }
      this.actionLoading = true
      try {
        await this.action()
        this.handleDisableOnSuccess()
      } catch (e) {
        Promise.reject(e)
      }
      this.actionLoading = false
    },
    handleDisableOnSuccess() {
      if (this.disableOnSuccess > 0) {
        this.disabled = true
        this.clearRecoverDisabledTimeout()
        this.recoverDisabledTimeout = setTimeout(() => {
          this.disabled = false
        }, this.disableOnSuccess)
      }
    },
    clearRecoverDisabledTimeout() {
      this.recoverDisabledTimeout && clearTimeout(this.recoverDisabledTimeout)
    },
  },
}
</script>
